/* global jQuery */

(function ($) {
	"use strict";
	$(document).ready( function () {
		if (window.location.href.indexOf("/#/freescan") > -1) {
			$(".heroContent .heading--1").addClass("hidden");
			$(".heroContent .heading--1 + .heading--1").removeClass("hidden");

			$(".heroContent .q-default-hero__subheading").addClass("hidden");
			$(".heroContent .q-default-hero__subheading + .q-default-hero__subheading").removeClass("hidden");

			$(".heroContent .paragraph--1").addClass("hidden");
			$(".heroContent .paragraph--1 + .paragraph--1").removeClass("hidden");
		}
	});
}(jQuery));
